.head{
    display: flex;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 16px;
}
    .head__header{
        font-size: 3em;
        letter-spacing: 2px;
        margin: 0;
        font-family: 'Anton', sans-serif;
    }
    .head__caption{
        font-size: 1.3em;
        margin-top: 10px;
    }

@media screen and (min-width: 576px){
    .head{
        font-size: 20px;
    }
}
@media screen and (min-width: 992px){
    .head{
        font-size: 23px;
    }
    .head__caption{
        font-size: .9em;
    }
}
@media screen and (min-width: 1200px){
    .head{
        align-items: flex-start;
        margin-left: 30px;
    }
}